export default function A(grid, startNode, finishNode) {
    let lastNode = grid[finishNode.m][finishNode.n]
    let queue = [grid[startNode.m][startNode.n]]
    let visitedNodes = []
    while(queue.length) {
        queue.sort((a, b) => {
            return a.distance - b.distance;
        });
        let currentNode = queue.shift()
        if(currentNode.isWall) continue;
        if(currentNode === lastNode) {
            let path = shortestPath(lastNode);
            return {visitedNodes, path}
        }
        visitedNodes.push(currentNode)
        currentNode.visited = true;
        let neighbors = getNeighbors(currentNode, grid)
        for(let neighbor of neighbors) {
            if(!neighbor.visited) {
                let min_distance = calculate_minhatan_distance(neighbor.row, finishNode.m,neighbor.col, finishNode.n)
                neighbor.distance = min_distance;
                neighbor.previous = currentNode;
                queue.push(neighbor)
            }
        }
    }
}

function shortestPath(finishNode) {
    let path = [];
    let pre = finishNode.previous;
    while (pre !== null) {
      path.push(pre);
      pre = pre.previous;
    }
    return path.reverse();
  }
  

function getNeighbors(currentNode, map) {
    let neighbors = [];
    if (currentNode.row > 0) {
      neighbors.push(map[currentNode.row - 1][currentNode.col]);
    }
    if (currentNode.row < map.length - 1) {
      neighbors.push(map[currentNode.row + 1][currentNode.col]);
    }
    if (currentNode.col > 0) {
      neighbors.push(map[currentNode.row][currentNode.col - 1]);
    }
    if (currentNode.col < map[0].length - 1) {
      neighbors.push(map[currentNode.row][currentNode.col + 1]);
    }
    return neighbors;
  }

function calculate_minhatan_distance(m1, m2, n1, n2) {
    return Math.abs(m1-m2)+Math.abs(n1-n2)
}